body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5; 
}

header {
  background: linear-gradient(45deg, #212121 80%, #ffb81c 20%) !important;
}
header h6 {
  font-weight: 400 !important;
  color: #fff !important;
}
header button {
  color: #fff !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Add task dialog */
.toolbar_addTask {
  margin-left: 16px;
}
/*.toolbar_addTask .add-button {
  color: #fff;
  background-color: #3bacef;
}*/
.toolbar_addTask svg {
  padding-right: 8px;
}

.dialog-addTask_title, .dialog-addTask_content {
  background: #f5f5f5;
}
.dialog-addTask .input-field {
  margin-top: 16px;
  margin-right: 16px;
}
.dialog-addTask .input-field:last-of-type {
  margin-right: 0px;
}
.input-field {
  background: #fff;
  width: 126px;
}

.dialog-addTask .input-field.input-field_text {
  width: 100%;
  margin-bottom: 16px;
}

.dialog-addTask .input-field_type {
  width: 100%;
  display: flex;
  margin-top: 16px;
}

.addTask_score {
  width: 75%;
  height: 64px;
  padding: 16px;
}

.dialog-addTask .dialog-score {
  margin-top: 16px;
}

/* Data table */
.RICEDataTable {
  display: flex;
  margin: 8px;
}
.RICEDataTable > div {
  width: 100%
}

.RICEDataTable .add-button {

}
